<template>
  <div>
    <div
      class="title"
      v-if="!isEditing"
    >
      <h3>产品管理</h3>
      <el-button
        class="pull-right ml20 "
        type="primary"
        @click="clickAddProduct()"
      >
        添加产品
      </el-button>
      <div class="search-box">
        <el-input
          v-model="keyWord"
          placeholder="请输入关键字"
        ></el-input>
        <el-button
          type="text"
          class="el-icon-search"
          @click="initData()"
        >
        </el-button>
      </div>
    </div>
    <div
      class="flex"
      v-if="!isEditing"
    >
      <div class="left-wrapper">
        <el-tree
          node-key="id"
          ref="tree"
          default-expand-all
          v-loading="treeLoading"
          :data="treeData"
          :expand-on-click-node="false"
          :check-on-click-node="true"
          :highlight-current="true"
          :current-node-key="curNode.id"
          @current-change="(node)=>{initData(1,node)}"
        >
          <span
            class="custom-tree-node"
            slot-scope="{ node, data }"
          >
            <span class="node-label">{{ node.label }}</span>
            <span>
              <el-button
                type="text"
                @click="() => clickAppendNode(data)"
              >
                <i class="el-icon-circle-plus-outline"></i>
              </el-button>
              <el-button
                type="text"
                @click="() => clickEditNode(data)"
              >
                <i class="el-icon-edit-outline"></i>
              </el-button>
              <el-button
                type="text"
                @click="() => clickDeleteNode(data)"
              >
                <i class="el-icon-delete"></i>
              </el-button>
            </span>
          </span>
        </el-tree>
      </div>
      <div class="right-wrapper">

        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="prodName"
            label="产品名称"
          >
          </el-table-column>
          <el-table-column
            prop="servName"
            label="包含服务"
          >
          </el-table-column>
          <el-table-column
            prop="brandLabel"
            label="提供者"
          >
          </el-table-column>
          <el-table-column
            prop="orderCycle"
            label="操作"
          >
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="clickEditProduct(scope.row)"
              >编辑</el-button>
              <el-button
                type="text"
                size="small"
                @click="clickDeleteProduct(scope.row)"
              >删除</el-button>
              <el-button
                type="text"
                size="small"
                v-if="scope.row.status==1"
                @click="clickRemoveProduct(scope.row)"
              >下架</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          class="mt20 pull-right"
          background
          v-if="tableData.length>0"
          layout="prev, pager, next"
          :current-page="pageNo"
          :page-size="pageSize"
          :total="total"
          @current-change="(pageNo)=>{initData(pageNo)}"
        >
        </el-pagination>
      </div>
    </div>
    <ProductEdit
      v-if="isEditing"
      v-model="isEditing"
      :curNode="curNode"
      :curRow="curProductInfo"
    ></ProductEdit>
  </div>
</template>

<script>
import ProductEdit from "./ProductEdit.vue";
export default {
  name: "ProductManagement",
  components: { ProductEdit },
  data() {
    return {
      keyWord: "",
      isEditing: false,
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
      treeLoading: false,
      treeData: [],
      curNode: {}
    };
  },
  mounted() {
    this.initNodeData();
  },
  methods: {
    initNodeData() {
      this.treeLoading = true;
      this.axios({
        url: "/a/operate/products/listMenu",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          this.treeLoading = false;
          if (res.data.transCode === "XX00") {
            this.treeData = res.data.data;
            if (this.treeData.length > 0) {
              this.$nextTick(() => {
                this.$refs.tree.setCurrentKey(this.treeData[0].id);
                this.initData(1, this.treeData[0]);
              });
            }
          }
        })
        .catch(err => {
          this.treeLoading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initData(pageNo, node) {
      this.pageNo = pageNo || 1;
      this.curNode = node ? node : this.curNode;
      this.loading = true;
      this.axios({
        url: "/a/operate/products/list",
        method: "post",
        params: {
          sign: 1,
          fatherId: this.curNode.id,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWord
        }
      })
        .then(res => {
          this.loading = false;
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    appendNode(node, nodeName) {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.1)"
      });
      this.axios({
        url: "/a/operate/products/addMenu",
        method: "post",
        params: {
          sign: 1,
          parentLabel: node.id,
          newName: nodeName
        }
      })
        .then(res => {
          loading.close();
          if (res.data.transCode === "XX00") {
            if (!node.children) {
              this.$set(node, "children", []);
              if (node.id === 1) {
                this.initNodeData();
              } else {
                node.children.push(res.data.data);
              }
            } else {
              node.children.push(res.data.data);
            }
            this.$nextTick(() => {
              this.$refs.tree.setCurrentKey(res.data.data.id);
              this.initData(1, this.treeData[0]);
            });
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          loading.close();
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    deleteNode(node) {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.1)"
      });
      this.axios({
        url: "/a/operate/products/deleteMenu",
        method: "post",
        params: {
          sign: 1,
          id: node.id
        }
      })
        .then(res => {
          loading.close();
          if (res.data.transCode === "XX00") {
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.initNodeData();
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          loading.close();
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    editNode(node, nodeName) {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.1)"
      });
      this.axios({
        method: "post",
        url: "/a/operate/products/addMenu",
        params: {
          sign: 1,
          id: node.id,
          newName: nodeName
        }
      })
        .then(res => {
          loading.close();
          if (res.data.transCode === "XX00") {
            node.label = nodeName;
            this.$message({
              type: "success",
              message: "修改成功"
            });
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          loading.close();
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    clickDeleteNode(node) {
      this.$confirm("确认删除该产品类型, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteNode(node);
        })
        .catch(() => {});
    },
    clickAppendNode(node) {
      this.$prompt("请输入节点名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(({ value }) => {
          this.appendNode(node, value);
        })
        .catch(() => {});
    },
    clickEditNode(node) {
      this.$prompt("请输入节点名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputValue: node.label
      })
        .then(({ value }) => {
          this.editNode(node, value);
        })
        .catch(() => {});
    },
    clickAddProduct() {
      if (this.curNode.id) {
        this.curProductInfo = {};
        this.isEditing = true;
      } else {
        this.$alert("请先选择左侧产品类型", "提示", {
          confirmButtonText: "确定"
        });
      }
    },
    clickEditProduct(row) {
      this.curProductInfo = row;
      this.isEditing = true;
    },
    clickDeleteProduct(row) {
      this.$confirm("确认删除该产品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteProduct(row);
        })
        .catch(() => {});
    },
    deleteProduct(row) {
      this.axios({
        method: "post",
        url: "/a/operate/products/deleteProduct",
        params: {
          sign: 1,
          productId: row.productId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.initData(1);
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    clickRemoveProduct(row) {
      this.$confirm("确认下架该产品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.removeProduct(row);
        })
        .catch(() => {});
    },
    removeProduct(row) {
      this.axios({
        method: "post",
        url: "/a/operate/products/undercarriage",
        params: {
          sign: 1,
          productId: row.productId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.$message({
              type: "success",
              message: row.prodName + "下架成功"
            });
            this.initData(1);
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    clickDeleteProduct(row) {
      this.$confirm("确认删除该产品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteProduct(row);
        })
        .catch(() => {});
    },
    deleteProduct(row) {
      this.axios({
        method: "post",
        url: "/a/operate/products/deleteProduct",
        params: {
          sign: 1,
          productId: row.productId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.initData(1);
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    }
  },
  watch: {
    isEditing(val, oldval) {
      if (val != oldval && !val) {
        this.initData(1);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
</style>



