<template>
  <div>
    <div
      class="title"
      v-if="!isEditing"
    >
      <h3>服务类别设置</h3>
      <el-button
        @click="clickAddService()"
        class="pull-right"
        type="primary"
      >
        新增服务
      </el-button>
      <div class="search-box mr20">
        <el-input
          v-model="keyWork"
          placeholder="请输入关键字"
        ></el-input>
        <el-button
          type="text"
          class="el-icon-search"
          @click="initData()"
        >
        </el-button>
      </div>
    </div>
    <div
      class="flex"
      v-if="!isEditing"
    >
      <div class="left-wrapper">
        <el-tree
          :data="treeData"
          v-loading="treeLoading"
          node-key="id"
          ref="tree"
          default-expand-all
          :expand-on-click-node="false"
          :check-on-click-node="true"
          :highlight-current="true"
          @current-change="(node)=>{initData(1,node)}"
          :current-node-key="curNode.id"
        >
          <span
            class="custom-tree-node"
            slot-scope="{ node, data }"
          >
            <span class="node-label">{{ node.label }}</span>
            <span>
              <el-button
                type="text"
                @click="() => clickAppendNode(data)"
              >
                <i class="el-icon-circle-plus-outline"></i>
              </el-button>
              <el-button
                type="text"
                @click="() => clickEditNode(data)"
              >
                <i class="el-icon-edit-outline"></i>
              </el-button>
              <el-button
                type="text"
                @click="() => clickDeleteNode(data)"
              >
                <i class="el-icon-delete"></i>
              </el-button>
            </span>
          </span>
        </el-tree>
      </div>
      <div class="right-wrapper">

        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="index"
            label="序号"
            width="80"
          >
          </el-table-column>
          <el-table-column
            prop="servName"
            label="服务名称"
          >
          </el-table-column>
          <el-table-column
            prop="statusLabel"
            label="服务状态"
          >
          </el-table-column>
          <el-table-column
            prop="serviceTypeLabel"
            label="服务类型"
          >
          </el-table-column>
          <el-table-column
            prop="orderCycle"
            label="操作"
          ><template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="clickEditService(scope.row)"
              >编辑</el-button>
              <el-button
                type="text"
                size="small"
                @click="clickDeleteService(scope.row)"
              >删除</el-button>

            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          class="mt20 pull-right"
          v-if="tableData.length>0"
          layout="prev, pager, next"
          :current-page="pageNo"
          :page-size="pageSize"
          :total="total"
          @current-change="initData"
        >
        </el-pagination>
      </div>
    </div>
    <ServiceEdit
      v-if="isEditing"
      v-model="isEditing"
      :curNode="curNode"
      :curRow="curServiceInfo"
    ></ServiceEdit>

  </div>
</template>

<script>
import ServiceEdit from "./ServiceEdit.vue";
export default {
  name: "ServiceManagement",
  components: { ServiceEdit },
  data() {
    return {
      keyWork: "",
      isEditing: false,
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
      treeData: [],
      treeLoading: false,
      curNode: {},
      curServiceInfo: {}
    };
  },

  mounted() {
    this.initNodeData();
  },
  methods: {
    initNodeData() {
      this.treeLoading = true;
      this.axios({
        url: "/a/operate/serServices/menuList",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          this.treeLoading = false;
          if (res.data.transCode === "XX00") {
            this.treeData = res.data.data;
            if (this.treeData.length > 0) {
              this.$nextTick(() => {
                this.$refs.tree.setCurrentKey(this.treeData[0].id);
                this.initData(1, this.treeData[0]);
              });
            }
          }
        })
        .catch(err => {
          this.treeLoading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initData(pageNo, node) {
      this.pageNo = pageNo || 1;
      this.curNode = node ? node : this.curNode;
      this.loading = true;
      this.axios({
        url: "/a/operate/serServices/list",
        method: "post",
        params: {
          sign: 1,
          id: this.curNode.id,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWork
        }
      })
        .then(res => {
          this.loading = false;
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    appendNode(node, nodeName) {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.1)"
      });
      this.axios({
        url: "/a/operate/serServices/AddMenu",
        method: "post",
        params: {
          sign: 1,
          parentLabel: node.id,
          newName: nodeName
        }
      })
        .then(res => {
          loading.close();
          if (res.data.transCode === "XX00") {
            if (!node.children) {
              this.$set(node, "children", []);
              if (node.id === 1) {
                this.initNodeData();
              } else {
                node.children.push(res.data.data);
              }
            } else {
              node.children.push(res.data.data);
            }
            this.initNodeData();
            this.$nextTick(() => {
              this.$refs.tree.setCurrentKey(res.data.data.id);
              this.initData(1, this.treeData[0]);
            });
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          loading.close();
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    deleteNode(node) {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.1)"
      });
      this.axios({
        url: "/a/operate/serServices/DeleteMenu",
        method: "post",
        params: {
          sign: 1,
          id: node.id
        }
      })
        .then(res => {
          loading.close();
          if (res.data.transCode === "XX00") {
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.initNodeData();
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          loading.close();
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    editNode(node, nodeName) {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.1)"
      });
      this.axios({
        method: "post",
        url: "/a/operate/serServices/EditMenu",
        params: {
          sign: 1,
          id: node.id,
          newName: nodeName
        }
      })
        .then(res => {
          loading.close();
          if (res.data.transCode === "XX00") {
            node.label = nodeName;
            this.$message({
              type: "success",
              message: "修改成功"
            });
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          loading.close();
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    clickDeleteNode(node) {
      this.$confirm("确认删除该服务类型, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteNode(node);
        })
        .catch(() => {});
    },
    clickAppendNode(node) {
      this.$prompt("请输入节点名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(({ value }) => {
          this.appendNode(node, value);
        })
        .catch(() => {});
    },
    clickEditNode(node) {
      this.$prompt("请输入节点名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputValue: node.label
      })
        .then(({ value }) => {
          this.editNode(node, value);
        })
        .catch(() => {});
    },
    clickAddService() {
      if (this.curNode.id) {
        this.curServiceInfo = {};
        this.isEditing = true;
      } else {
        this.$alert("请先选择左侧服务类型", "提示", {
          confirmButtonText: "确定"
        });
      }
    },
    clickEditService(row) {
      this.curServiceInfo = row;
      this.isEditing = true;
    },
    clickDeleteService(row) {
      this.$confirm("确认删除该服务, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteService(row);
        })
        .catch(() => {});
    },
    deleteService(row) {
      this.axios({
        method: "post",
        url: "/a/operate/serServices/delete",
        params: {
          sign: 1,
          id: row.serviceId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.initData(1);
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    }
  },
  watch: {
    isEditing(val, oldval) {
      if (val != oldval && !val) {
        this.initData(1);
      }
    }
  }
};
</script>

<style lang="less" scoped>
</style>



