import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false
    // import $ from 'jquery';

Vue.use(ElementUI);

// import "../plugins/jquery/jquery-3.3.1.min.js";
// import "../plugins/zTree/js/jquery.ztree.core.min.js";
