import Vue from 'vue'
import Router from 'vue-router'
import ServiceManagement from '@/components/service/ServiceManagement'
import ProductManagement from '@/components/product/ProductManagement'
import OrderManagement from '@/components/OrderManagement'
import ContractManagement from '@/components/ContractManagement'
import UserManagement from '@/components/user/UserManagement'
import UnsubscribeManagement from '@/components/UnsubscribeManagement'
import InvoiceManagement from '@/components/InvoiceManagement'


Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        redirect: '/serviceManagement'
    }, {
        path: '/serviceManagement',     // 服务管理
        name: 'ServiceManagement',
        component: ServiceManagement    
    }, {
        path: '/productManagement',     // 产品管理
        name: 'ProductManagement',
        component: ProductManagement    
    }, {
        path: '/orderManagement',       // 订单管理
        name: 'OrderManagement',
        component: OrderManagement
    }, {
        path: '/contractManagement',     // 合同管理
        name: 'ContractManagement',
        component: ContractManagement
    }, {
        path: '/userManagement',         // 客户管理
        name: 'UserManagement',
        component: UserManagement
    }, {
        path: '/unsubscribeManagement',  // 退订管理
        name: 'UnsubscribeManagement',
        component: UnsubscribeManagement
    }, {
        path: '/invoiceManagement',      // 发票管理
        name: 'InvoiceManagement',
        component: InvoiceManagement
    }]
})


