<template>
  <div>
    <div
      class="title"
      v-if="!isEditing"
    >
      <h3>客户管理</h3>
      <div class="search-box">
        <el-input
          v-model="keyWord"
          placeholder="请输入关键字"
        ></el-input>
        <el-button
          type="text"
          class="el-icon-search"
          @click="initData()"
        >
        </el-button>
      </div>
    </div>
    <div
      class="flex"
      v-if="!isEditing"
    >
      <div class="left-wrapper">
        <el-tree
          node-key="id"
          ref="tree"
          default-expand-all
          v-loading="treeLoading"
          :data="treeData"
          :expand-on-click-node="false"
          :check-on-click-node="true"
          :highlight-current="true"
          :current-node-key="curNode.id"
          @current-change="(node)=>{initData(1,node)}"
        >
          <span
            class="custom-tree-node"
            slot-scope="{ node }"
          >
            <span class="node-label">{{ node.label }}</span>
          </span>
        </el-tree>
      </div>
      <div class="right-wrapper">

        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="companyName"
            label="客户名称"
          >
          </el-table-column>
          <el-table-column
            prop="address"
            label="地址"
          >
          </el-table-column>
          <el-table-column
            prop="userName"
            label="联系人"
          >
          </el-table-column>
          <el-table-column
            prop="phone"
            label="联系方式"
          >
          </el-table-column>

          <el-table-column
            prop="orderCycle"
            label="操作"
          >
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="getOrder(1,scope.row)"
              >订购列表</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          class="mt20 pull-right"
          background
          v-if="tableData.length>0"
          layout="prev, pager, next"
          :current-page="pageNo"
          :page-size="pageSize"
          :total="total"
          @current-change="(pageNo)=>{initData(pageNo)}"
        >
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="订购列表"
      :visible.sync="dialogVisible"
      width="80%"
    >
      <el-table
        v-loading="loadingDialog"
        :data="tableDataDialog"
        border
        stripe
        style="width: 100%"
      >
        <el-table-column
          prop="productName"
          label="订购产品名称"
        >
        </el-table-column>
        <el-table-column
          prop="orderId"
          label="订单号"
        >
        </el-table-column>
        <el-table-column
          prop="productType"
          label="产品类型"
        >
        </el-table-column>
        <el-table-column
          prop="orderNo"
          label="订购数"
        >
        </el-table-column>
        <el-table-column
          prop="servStartDate"
          label="服务开通时间"
        >
        </el-table-column>
        <el-table-column
          prop="servEndDate"
          label="服务到期时间"
        >
        </el-table-column>
        <el-table-column
          prop="operation"
          label="运行状态"
        >
          <template slot-scope="scope">
            <span :class="{'warning':scope.row.serviceStatus=='1'||scope.row.serviceStatus=='3','success':scope.row.serviceStatus=='2','info':scope.row.serviceStatus=='4'}">{{scope.row.operation}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="orderCycle"
          label="操作"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="curOrder=scope.row; dialogVisibleStatus= true;serviceStatus = scope.row.serviceStatus"
            >状态变更</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        class="mt20 "
        background
        v-if="tableDataDialog.length>0"
        layout="prev, pager, next"
        :current-page="pageNoDialog"
        :page-size="pageSizeDialog"
        :total="totalDialog"
        @current-change="(pageNo)=>{getOrder(pageNo)}"
      >
      </el-pagination>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="default"
          @click="dialogVisible = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="状态变更"
      :visible.sync="dialogVisibleStatus"
      width="400px"
    >
      <el-form
        ref="from"
        label-width="100px"
      >
        <el-form-item label="客户名称:">{{curRow.companyName}}
        </el-form-item>
        <el-form-item label="订单号:">
          {{curOrder.orderId}}
        </el-form-item>
        <el-form-item label="开通状态:">
          <el-select
            v-model="serviceStatus"
            placeholder="请选择"
          >
            <el-option
              label="待开通"
              value="1"
            >
            </el-option>
            <el-option
              label="使用中"
              value="2"
            >
            </el-option>
            <el-option
              label="退订中"
              value="3"
            >
            </el-option>
            <el-option
              label="已停止"
              value="4"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleStatus = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="saveOrderStatus()"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "UserManagement",
  data() {
    return {
      keyWord: "",
      isEditing: false,
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,

      loadingDialog: false,
      tableDataDialog: [],
      pageNoDialog: 1,
      pageSizeDialog: 5,
      totalDialog: 0,

      treeLoading: false,
      treeData: [],
      curNode: {},
      dialogVisible: false,
      dialogVisibleStatus: false,
      curRow: {},
      curOrder: {},
      serviceStatus: 1
    };
  },
  mounted() {
    this.initNodeData();
  },
  methods: {
    initNodeData() {
      this.treeLoading = true;
      this.axios({
        url: "/a/operate/serviceWorkOrder/getProvince",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          this.treeLoading = false;
          if (res.data.transCode === "XX00") {
            this.treeData = res.data.data;
            if (this.treeData.length > 0) {
              this.$nextTick(() => {
                this.$refs.tree.setCurrentKey(this.treeData[0].id);
                this.initData(1, this.treeData[0]);
              });
            }
          }
        })
        .catch(err => {
          this.treeLoading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initData(pageNo, node) {
      this.pageNo = pageNo || 1;
      this.curNode = node ? node : this.curNode;
      this.loading = true;
      this.axios({
        url: "/a/operate/serviceWorkOrder/getProvinceUser",
        method: "post",
        params: {
          sign: 1,
          id: this.curNode.id,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWord
        }
      })
        .then(res => {
          this.loading = false;
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    getOrder(pageNo, row) {
      this.loadingDialog = true;
      this.pageNoDialog = pageNo || this.pageNoDialog;
      this.curRow = row || this.curRow;
      this.axios({
        url: "/a/operate/serviceWorkOrder/getProductByUser",
        method: "post",
        params: {
          sign: 1,
          custId: this.curRow.custId,
          pageNo: this.pageNoDialog,
          pageSize: this.pageSizeDialog
        }
      })
        .then(res => {
          this.loadingDialog = false;
          if (res.data.transCode === "XX00") {
            this.tableDataDialog = res.data.data;
            this.totalDialog = res.data.total;
            this.dialogVisible = true;
          }
        })
        .catch(err => {
          this.loadingDialog = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    saveOrderStatus() {
      this.axios({
        url: "/a/operate/serviceWorkOrder/save",
        method: "post",
        params: {
          sign: 1,
          workId: this.curOrder.workId,
          status: this.serviceStatus
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.getOrder();
            this.dialogVisibleStatus = false;
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
</style>



