<template>
  <div class="full-wrapper">

    <div class="title">
      <h3>订单管理</h3>
    </div>
    <div>
      <el-form
        class="demo-form-inline mb20"
        label-width="80px"
      >
        <el-row>
          <el-col
            :lg="4"
            :sm="8"
          >
            <el-form-item
              class=""
              label="产品类型"
            >
              <el-select
                placeholder="产品类型"
                v-model="searchCondition.productType"
                @change="initData()"
              >
                <el-option
                  v-for="(v, i) in productTypeList"
                  :key="i"
                  :label="v.label"
                  :value="v.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col
            :lg="4"
            :sm="8"
          >
            <el-form-item label="订单类型">
              <el-select
                placeholder="订单类型"
                v-model="searchCondition.orderType"
                @change="initData()"
              >
                <el-option
                  v-for="(v, i) in orderTypeList"
                  :key="i"
                  :label="v.label"
                  :value="v.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col
            :lg="4"
            :sm="8"
          >
            <el-form-item label="支付状态">
              <el-select
                placeholder="支付状态"
                v-model="searchCondition.payType"
                @change="initData()"
              >
                <el-option
                  v-for="(v, i) in payTypeList"
                  :key="i"
                  :label="v.label"
                  :value="v.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col
            :lg="5"
            :sm="8"
          >
            <el-form-item
              class="date-selector-box"
              label="起始时间"
            >
              <el-col :span="11">
                <el-date-picker
                  type="date"
                  placeholder="开始日期"
                  style="width: 100%;"
                  v-model="searchCondition.startDate"
                  @change="initData()"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-col>
              <el-col
                class="line text-center"
                :span="2"
              >-</el-col>
              <el-col :span="11">
                <el-date-picker
                  placeholder="结束日期"
                  style="width: 100%;"
                  v-model="searchCondition.endDate"
                  @change="initData()"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col
            :lg="5"
            :sm="10"
            class="search-col"
          >
            <el-form-item label-width="0px">
              <div class="search-box">
                <el-input
                  placeholder="请输入关键字"
                  v-model="searchCondition.keyWord"
                  @keyup.enter="initData()"
                ></el-input>
                <el-button
                  type="text"
                  class="el-icon-search"
                  @click="initData()"
                >
                </el-button>
              </div>
            </el-form-item>
          </el-col>
          <el-col
            :lg="1"
            :sm="2"
          >
            <el-form-item label-width="0px">
              <el-button type="primary"> <a
                  type="primary"
                  class="btn"
                  :href="'http://172.22.79.39:8080/a/operate/custOrderItem/export?sign=2&orderType='
                  +searchCondition.orderType+'&productType='+searchCondition.productType+'&status='+searchCondition.payType+'&startDate='
                  +searchCondition.startDate+'&endDate='+searchCondition.endDate+'&orderProduct='+searchCondition.keyWord"
                >导出Excel</a></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table
      v-loading="loading"
      :data="tableData"
      border
      stripe
      style="width: 100%"
      class="mb20"
    >
      <el-table-column
        prop="custName"
        label="客户名称"
      >
      </el-table-column>
      <el-table-column
        prop="channel"
        label="订单类型"
      >
      </el-table-column>
      <el-table-column
        prop="orderId"
        label="订单号"
      >
      </el-table-column>
      <el-table-column
        prop="orderProduct"
        label="产品"
      >
      </el-table-column>
      <el-table-column
        prop="orderSlGrade"
        label="服务等级"
      >
      </el-table-column>
      <el-table-column
        prop="orderBilling"
        label="计费方式"
      >
      </el-table-column>
      <el-table-column
        prop="orderCycle"
        label="订购周期"
      >
      </el-table-column>
      <el-table-column
        prop="orderNo"
        label="订购数量"
      >
      </el-table-column>
      <el-table-column
        prop="addDate"
        label="创建时间"
      >
      </el-table-column>
      <el-table-column
        prop="statusDate"
        label="支付时间"
      >
      </el-table-column>
      <el-table-column
        prop="statusLabel"
        label="状态"
      >
        <template slot-scope="scope">
          <span :class="{'warning':scope.row.status=='1'}">{{scope.row.statusLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="money"
        label="应付金额"
      >
      </el-table-column>
      <el-table-column
        prop="orderCycle"
        label="操作"
      ><template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="()=>{dialogVisibleDetail = true; curRow =scope.row;}"
          >详情</el-button>
          <el-button
            type="text"
            size="small"
            v-if="scope.row.status === '1'"
            @click="()=>{dialogVisibleUpdatePrice = true; curRow =scope.row; moneyMsg= scope.row.money;}"
          >价格调整</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      v-if="tableData.length>0"
      layout="prev, pager, next"
      :current-page="pageNo"
      :page-size="pageSize"
      :total="total"
      @current-change="initData"
    >
    </el-pagination>
    <el-dialog
      title="订单详情"
      :visible.sync="dialogVisibleDetail"
      width="50%"
    >
      <el-form
        ref="form"
        label-width="100px"
      >
        <el-col :span="24">
          <el-form-item label="订单状态:">
            <span :class="{'warning':curRow.status=='1'}">{{curRow.statusLabel}}</span>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="订单号:">
            {{curRow.orderId}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="创建时间:">
            {{curRow.addDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="支付时间:">
            {{curRow.statusDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="产品名称:">
            {{curRow.orderProduct}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="付费类型:">
            {{curRow.orderBilling}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="数量:">
            {{curRow.orderNo}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="服务开始时间:">
            {{curRow.servStartDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="服务结束时间:">
            {{curRow.servEndDate}}
          </el-form-item>
        </el-col>
        <el-col
          :span="24"
          class="mb20"
        >
          <el-form-item label="应付金额:">
            {{curRow.money}}
          </el-form-item>
        </el-col>
      </el-form>

      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleDetail = false"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="价格调整"
      :visible.sync="dialogVisibleUpdatePrice"
      width="50%"
    >
      <el-form
        ref="form"
        label-width="100px"
      >
        <h3 class="mb20">{{curRow.custName}} 通过智讯云账号：{{curRow.phone}} 订购智讯云提供的如下产品/服务，正在进行价格调整：</h3>
        <el-col :span="24">
          <el-form-item label="订单状态:">
            <span :class="{'warning':curRow.status=='1'}">{{curRow.statusLabel}}</span>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="订单号:">
            {{curRow.orderId}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="创建时间:">
            {{curRow.addDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="支付时间:">
            {{curRow.statusDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="产品名称:">
            {{curRow.orderProduct}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="付费类型:">
            {{curRow.orderBilling}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="数量:">
            {{curRow.orderNo}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="服务开始时间:">
            {{curRow.servStartDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label="服务结束时间:">
            {{curRow.servEndDate}}
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="应付金额:">
            {{curRow.money}}
          </el-form-item>
        </el-col>
        <el-col
          :span="22"
          class="mb20"
        >
          <el-form-item label="修改金额:">
            <el-input
              v-model="moneyMsg"
              type="number"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleUpdatePrice = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="updatePrice()"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "OrderManagement",
  data() {
    return {
      productTypeList: [],
      orderTypeList: [],
      payTypeList: [],
      searchCondition: {
        productType: "",
        orderType: "",
        payType: "",
        keyWord: "",
        startDate: "",
        endDate: ""
      },
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
      curRow: {},
      dialogVisibleDetail: false,
      dialogVisibleUpdatePrice: false,
      moneyMsg: ""
    };
  },
  mounted() {
    this.initProductSelect();
    this.initOrderSelect();
    this.initPaySelect();
    this.initData(1);
  },
  methods: {
    initProductSelect() {
      this.axios({
        url: "/a/operate/custOrderItem/getProductType",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.productTypeList = res.data.data;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initOrderSelect() {
      this.axios({
        url: " /a/operate/custOrderItem/getOrderType",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.orderTypeList = res.data.data;
            this.orderType = this.orderTypeList[0].vlaue;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initPaySelect() {
      this.axios({
        url: " /a/operate/custOrderItem/getPayStatus",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.payTypeList = res.data.data;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initData(pageNo) {
      this.pageNo = pageNo || 1;
      this.axios({
        url: "/a/operate/custOrderItem/findQuery",
        method: "post",
        params: {
          sign: 2,
          orderType: this.searchCondition.orderType,
          productType: this.searchCondition.productType,
          status: this.searchCondition.payType,
          startDate: this.searchCondition.startDate,
          endDate: this.searchCondition.endDate,
          orderProduct: this.searchCondition.keyWord,
          pageNo: this.pageNo,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data.data;
            this.total = res.data.data.total;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    updatePrice() {
      this.axios({
        url: "/a/operate/custOrderItem/updateMoney",
        method: "post",
        params: {
          sign: 2,
          custOrderId: this.curRow.custOrderId,
          moneyMsg: this.moneyMsg
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.dialogVisibleUpdatePrice = false;
            this.initData(this.pageNo);
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    changePage() {}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.search-box {
  float: inherit !important;
  width: auto !important;
  margin: 0 20px;
}
.el-button a {
  text-decoration: none;
  color: #ffffff;
}
.el-form-item {
  margin-bottom: 20px;
}
* #app .title {
  border-bottom: none;
}

form {
  overflow: hidden;
}
</style>



