<template>
  <div class="full-wrapper">
    <div class="title">
      <h3>合同管理</h3>
      <el-form
        class="demo-form-inline mb20"
        label-width="80px"
      >
        <el-row>
          <el-form-item
            label-width="0px"
            class=" pull-right"
          >
            <el-button type="primary">
              <a
                type="primary"
                class="btn"
                href="/a/operate/custContract/standContractDownload?sign=1"
              >合同模板下载</a>
            </el-button>
          </el-form-item>
          <el-col
            :lg="5"
            :sm="10"
            class="search-col pull-right"
          >
            <el-form-item label-width="0px">
              <div class="search-box">
                <el-input
                  placeholder="请输入关键字"
                  v-model="keyWord"
                  @keyup.enter="initData()"
                ></el-input>
                <el-button
                  type="text"
                  class="el-icon-search"
                  @click="initData()"
                >
                </el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="main-content">

      <el-badge
        :value="undealTotal"
        class="item"
      ></el-badge>
      <el-tabs
        v-model="activeIndex"
        @tab-click="()=>{initData()}"
      >

        <el-tab-pane
          label="全部"
          name="1"
        >
        </el-tab-pane>

        <el-tab-pane
          label="未处理"
          name="2"
        ></el-tab-pane>
        <el-tab-pane
          label="已处理"
          name="3"
        ></el-tab-pane>
      </el-tabs>
      <div>

      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
        border
        stripe
        style="width: 100%"
        class="mb20"
      >
        <el-table-column
          prop="companyName"
          label="企业名称"
        >
        </el-table-column>
        <el-table-column
          prop="contacter"
          label="联系人"
        >
        </el-table-column>
        <el-table-column
          prop="phone"
          label="联系电话"
        >
        </el-table-column>
        <el-table-column
          prop="address"
          label="联系地址"
        >
        </el-table-column>
        <el-table-column
          prop="mailbox"
          label="联系人邮箱"
        >
        </el-table-column>
        <el-table-column
          prop="orderId"
          label="订单号"
        >
        </el-table-column>
        <el-table-column
          prop="orderProduct"
          label="订购产品"
        >
        </el-table-column>
        <el-table-column
          prop="orderSlGrade"
          label="服务等级"
        >
        </el-table-column>
        <el-table-column
          prop="money"
          label="支付金额"
        >
        </el-table-column>
        <el-table-column
          prop="dealStatusLabel"
          label="状态"
        >
          <template slot-scope="scope">
            <span :class="{'warning':scope.row.dealStatus=='1'}">{{scope.row.dealStatusLabel}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="orderCycle"
          label="操作"
        ><template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              v-if="scope.row.dealStatus === '1'"
              @click="()=>{dialogVisibleDetail=true; isEdit=true; curContractInfo=scope.row;dealStatus=false;}"
            >查看申请</el-button>
            <el-button
              type="text"
              size="small"
              v-if="scope.row.dealStatus === '2'"
              @click="()=>{dialogVisibleDetail=true; isEdit=false; curContractInfo=scope.row;dealStatus=false;}"
            >详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      v-if="tableData.length>0"
      layout="prev, pager, next"
      :current-page="pageNo"
      :page-size="pageSize"
      :total="total"
      @current-change="initData"
    >
    </el-pagination>
    <el-dialog
      title="客户合同申请"
      :visible.sync="dialogVisibleDetail"
      width="50%"
    >
      <p>
        当前状态：<span :class="{'warning':curContractInfo.dealStatus=='1'}">{{curContractInfo.dealStatusLabel}}</span>
        <el-checkbox
          v-if="isEdit"
          class="pull-right"
          v-model="dealStatus"
        >合同已发送</el-checkbox>
      </p>
      <div>
        <h3> 企业信息</h3>
        <el-form
          ref="form"
          label-width="100px"
        >
          <el-col :span="12">
            <el-form-item label="申请日期:">
              {{curContractInfo.createTime}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="合同类型:">
              {{curContractInfo.ispaperLabel}}
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="公司名称:">
              {{curContractInfo.companyName}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系人:">
              {{curContractInfo.contacter}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话:">
              {{curContractInfo.phone}}
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="电子邮箱:">
              {{curContractInfo.mailbox}}
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="联系地址:">
              {{curContractInfo.address}}
            </el-form-item>
          </el-col>
        </el-form>
      </div>
      <div class="radius">
        <h3> 订单信息</h3>
        <el-form
          ref="form"
          label-width="100px"
        >
          <el-col :span="12">
            <el-form-item label="订单号:">
              {{curContractInfo.orderId}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="支付时间:">
              {{curContractInfo.payDate}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="产品名称:">
              {{curContractInfo.orderProduct}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="服务等级:">
              {{curContractInfo.orderSlGrade}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="付费类型:">
              {{curContractInfo.orderBilling}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="数量:">
              {{curContractInfo.orderNo}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="金额:">
              {{curContractInfo.money}}
            </el-form-item>
          </el-col>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="default"
          v-if="isEdit"
          @click="dialogVisibleDetail = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="saveContract()"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ContractManagement",
  data() {
    return {
      activeIndex: "1",
      isEdit: false,
      undealTotal: 0,
      keyWord: "",
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
      curContractInfo: {},
      dialogVisibleDetail: false,
      dealStatus: false
    };
  },
  mounted() {
    this.initData();
    this.initDataUndeal();
  },
  methods: {
    initData(pageNo) {
      this.loading = true;
      this.pageNo = pageNo || 1;
      this.axios({
        url: "/a/operate/custContract/getCustContractList",
        method: "post",
        params: {
          sign: this.activeIndex,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWord
        }
      })
        .then(res => {
          this.loading = false;
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data;
            this.total = res.data.total;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initDataUndeal() {
      this.axios({
        url: "/a/operate/custContract/getUndealContractCount",
        method: "post",
        params: {
          sign: this.activeIndex,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWord
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.undealTotal = res.data.data;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    // getContractInfo(id) {
    //   this.axios({
    //     url: "/a/operate/custContract/orderRecall",
    //     method: "post",
    //     params: {
    //       sign: 1,
    //       contractId: id
    //     }
    //   })
    //     .then(res => {
    //         debugger;
    //       if (res.data.transCode === "XX00") {
    //         this.curContractInfo = res.data.data;
    //       } else {
    //         this.$alert(res.data.msg, "提示", {
    //           confirmButtonText: "确定"
    //         });
    //       }
    //     })
    //     .catch(err => {
    //       this.$alert("服务器报错", "提示", {
    //         confirmButtonText: "确定"
    //       });
    //     });
    // },
    saveContract() {
      if (!this.isEdit || !this.dealStatus) {
        this.dialogVisibleDetail = false;
        return;
      }
      this.axios({
        url: "/a/operate/custContract/saveContract",
        method: "post",
        params: {
          sign: 2,
          dealStatus: this.dealStatus ? "2" : undefined,
          custId: this.curContractInfo.custId,
          contractId: this.curContractInfo.contractId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.dialogVisibleDetail = false;
            this.initData(this.pageNo);
            this.initDataUndeal();
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.search-box {
  float: inherit !important;
  width: auto !important;
  margin: 0 20px;
}

.el-button a {
  text-decoration: none;
  color: #ffffff;
}
.el-form-item {
  margin-bottom: 0px;
}
body #app .title {
  border-bottom: none;
}
.main-content {
  position: relative;
  .el-badge {
    position: absolute;
    &:nth-child(1) {
      left: 110px;
      top: 0px;
    }
  }
}
form {
  overflow: hidden;
}
.el-dialog__body {
  padding-top: 0;
}
</style>



