<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style lang="less">
body,
* {
  #app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    padding: 20px;
    .pull-right {
      float: right;
    }
    .mt20 {
      margin-top: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mb20 {
      margin-bottom: 20px;
    }

    .text-center {
      text-align: center;
    }
    .pull-right {
      float: right;
    }
    .ml20 {
      margin-left: 20px;
    }
    .success {
      color: #67c23a;
    }

    .warning {
      color: #e6a23c;
    }
    .danger {
      color: #f56c6c;
    }
    .info {
      color: #909399;
    }

    .search-box {
      float: right;
      width: 400px;
      border: 1px solid #dcdfe6;
      border-radius: 4px;
      height: 38px;
      overflow: hidden;
      .el-button {
        float: right;
        font-size: 20px;
        padding: 10px 10px;
      }
      .el-input {
        input {
          border: none;
        }
        width: calc(~"100% - 50px");
      }
    }
    .title {
      height: 40px;
      border-bottom: 1px solid #e1e1e1;
      padding-bottom: 20px;
      h3 {
        margin-top: 0;
        float: left;
        height: 40px;
        line-height: 40px;
      }
    }
    .flex {
      display: flex;
      position: absolute;
      height: calc(~"100% - 80px");
      width: calc(~"100% - 80px");
      .left-wrapper {
        min-width: 280px;
        padding-right: 20px;
        border-right: 1px solid #e1e1e1;
        margin-right: 20px;
        padding-top: 20px;
        .node-label {
          margin-right: 20px;
        }
        .el-button + .el-button {
          margin: 0;
        }
      }
      .right-wrapper {
        flex: 1;
        padding-top: 20px;
      }
    }
  }
}
</style>



