<template>
  <div class="hello">
    <h3> 产品创建</h3>
    <el-form
      ref="from"
      :model="productInfo"
      label-width="100px"
      :rules="rules"
    >
      <el-form-item
        label="产品名称"
        prop="prodName"
      >
        <el-input
          v-model="productInfo.prodName"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
      <el-form-item label="产品类型">
        <el-select
          v-model="productInfo.prodType"
          placeholder="请选择"
        >
          <el-option
            v-for="(item,i) in this.productTypeList"
            :key="i"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="产品提供方">
        <el-select
          v-model="productInfo.brand"
          placeholder="请选择"
        >
          <el-option
            v-for="(item,i) in this.providerList"
            :key="i"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="服务提供方">
        <el-input
          v-model="productInfo.servProvId"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
      <el-form-item label="计费方式">
        <el-select
          v-model="productInfo.pm"
          placeholder="请选择"
        >
          <el-option
            v-for="(item,i) in this.billFrequencyList"
            :key="i"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="是否是主产品">
        <el-select
          v-model="productInfo.isMain"
          placeholder="请选择"
        >
          <el-option
            label="是"
            value="1"
          > </el-option>
          <el-option
            label="否"
            value="0"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="服务选择">
        <el-col
          :span="6"
          class="mr20"
        >
          <el-input
            v-model="serviceInfo.checkLabel"
            placeholder="请输入内容"
            :disabled="true"
          ></el-input>
        </el-col>
        <el-button
          type="primary"
          @click="serviceInfo.dialogVisible = true;initService();"
        >选 择</el-button>
      </el-form-item>
      <el-form-item label="备注">
        <el-input
          v-model="productInfo.prodDesc"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
      <el-tabs
        type="card"
        class="mt20"
      >
        <el-tab-pane label="价格设置">
          <p v-if="!serviceInfo.checkId">提示：请先选择服务</p>
          <el-form-item
            :label="item.slName"
            v-for="(item,i) in priceInfo"
            :key="i"
          >
            <el-col
              :span="6"
              class="mr20"
            >
              <el-input
                type="number"
                v-model="item.channelPrice"
                placeholder="设置基础价格"
              ></el-input>
            </el-col>
            <el-col
              :span="6"
              class="mr20"
            >
              <el-input
                type="number"
                v-model="item.basePrice"
                placeholder="设置渠道价格"
              ></el-input>
            </el-col>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="产品信息">
          <el-form
            ref="from"
            :model="productInfo"
            label-width="100px"
            :rules="rules"
          >
            <el-form-item label="产品功能图：">
              <el-upload
                class="upload-demo"
                action="/a/operate/products/save"
                ref="upload"
                :auto-upload="false"
                list-type="picture"
                :file-list="fileList[0]"
                :on-change="(file, fileList)=>changeFile(file,fileList,0)"
                :on-remove="(file, fileList)=>changeFile(file,fileList,0)"
                accept=".jpg,.png"
              >
                <el-button
                  size="small"
                  type="primary"
                >点击上传</el-button>
                <div
                  slot="tip"
                  class="el-upload__tip"
                >只能上传jpg/png文件，且不超过500kb</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="产品说明：">
              <el-upload
                class="upload-demo"
                action="/a/operate/products/save"
                ref="upload"
                :auto-upload="false"
                list-type="picture"
                :file-list="fileList[1]"
                :on-change="(file, fileList)=>changeFile(file,fileList,1)"
                :on-remove="(file, fileList)=>changeFile(file,fileList,1)"
                accept=".jpg,.png"
              >
                <el-button
                  size="small"
                  type="primary"
                >点击上传</el-button>
                <div
                  slot="tip"
                  class="el-upload__tip"
                >只能上传jpg/png文件，且不超过500kb</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="使用指南：">
              <el-upload
                class="upload-demo"
                action="/a/operate/products/save"
                ref="upload"
                :auto-upload="false"
                list-type="fileList"
                :file-list="fileList[2]"
                :on-change="(file, fileList)=>changeFile(file,fileList,2)"
                :on-remove="(file, fileList)=>changeFile(file,fileList,2)"
                accept=".pdf,.doc,.docx,.txt"
              >
                <el-button
                  size="small"
                  type="primary"
                >点击上传</el-button>
                <div
                  slot="tip"
                  class="el-upload__tip"
                >只能上传pdf/doc/txt文件，且不超过2M</div>
              </el-upload>
            </el-form-item>
            <h4>客户案例</h4>
            <div class=""></div>
            <el-col :span="8">
              <el-form-item
                label-width="0"
                class="mr20"
              >
                <span>案例一：</span>
                <el-upload
                  class="upload-demo"
                  action="/a/operate/products/save"
                  ref="upload"
                  :auto-upload="false"
                  list-type="picture"
                  :limit="1"
                  :file-list="fileList[3]"
                  :on-change="(file, fileList)=>changeFile(file,fileList,3)"
                  :on-remove="(file, fileList)=>removeFile(file,fileList,3)"
                  :on-exceed="(file, fileList)=>handleExceed(file,fileList,3)"
                  :multiple="false"
                  accept=".jpg,.png"
                >
                  <el-button
                    size="small"
                    type="primary"
                  >点击上传</el-button>
                  <div
                    slot="tip"
                    class="el-upload__tip"
                  >只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
                <el-input
                  class="mt20"
                  type="textarea"
                  v-model="productInfo.exampleDesc1"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item
                label-width="0"
                class="mr20"
              >
                <span>案例二：</span>
                <el-upload
                  class="upload-demo"
                  action="/a/operate/products/save"
                  ref="upload"
                  :auto-upload="false"
                  list-type="picture"
                  :limit="1"
                  :file-list="fileList[4]"
                  :on-change="(file, fileList)=>changeFile(file,fileList,4)"
                  :on-remove="(file, fileList)=>removeFile(file,fileList,4)"
                  :on-exceed="(file, fileList)=>handleExceed(file,fileList,4)"
                  :multiple="false"
                  accept=".jpg,.png"
                >
                  <el-button
                    size="small"
                    type="primary"
                  >点击上传</el-button>
                  <div
                    slot="tip"
                    class="el-upload__tip"
                  >只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
                <el-input
                  class="mt20"
                  type="textarea"
                  v-model="productInfo.exampleDesc2"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label-width="0">
                <span>案例三：</span>
                <el-upload
                  class="upload-demo"
                  action="/a/operate/products/save"
                  ref="upload"
                  :auto-upload="false"
                  list-type="picture"
                  :limit="1"
                  :file-list="fileList[5]"
                  :on-change="(file, fileList)=>changeFile(file,fileList,5)"
                  :on-remove="(file, fileList)=>removeFile(file,fileList,5)"
                  :on-exceed="(file, fileList)=>handleExceed(file,fileList,5)"
                  :multiple="false"
                  accept=".jpg,.png"
                >
                  <el-button
                    size="small"
                    type="primary"
                  >点击上传</el-button>
                  <div
                    slot="tip"
                    class="el-upload__tip"
                  >只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
                <el-input
                  class="mt20"
                  type="textarea"
                  v-model="productInfo.exampleDesc3"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <div
      slot="footer"
      class="dialog-footer"
    >
      <el-button
        type="primary"
        plain
        @click="$emit('input',false)"
      >取 消</el-button>
      <el-button
        type="primary"
        @click="saveProduct()"
      >确 定</el-button>
    </div>
    <el-dialog
      title="服务选择"
      :visible.sync="serviceInfo.dialogVisible"
      width="500px"
      @closed="changeServiceStatus()"
    >
      <div class="search-box mb20">
        <el-input
          v-model="keyWord"
          placeholder="请输入关键字"
        ></el-input>
        <el-button
          type="text"
          class="el-icon-search"
          @click="initService()"
        >
        </el-button>
      </div>
      <div class="flex">
        <div class="left-wrapper">
          <div
            v-for="(item,i) in serviceInfo.list"
            :key="i"
            @click="serviceInfo.curService=item"
            :class="{'active':item.id1===serviceInfo.curService.id1}"
          >{{item.name1}}</div>
        </div>
        <div class="right-wrapper">
          <el-radio-group v-model="serviceInfo.checkId">
            <el-radio
              :label="v.serviceId"
              v-for="(v,i) in serviceInfo.curService.services"
              :key="i"
            >{{v.servName}}</el-radio>
          </el-radio-group>
        </div>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          plain
          @click="serviceInfo.dialogVisible = false; serviceInfo.checkId = serviceInfo.orginal;"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="serviceInfo.dialogVisible = false; serviceInfo.orginal = serviceInfo.checkId;"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import qs from "qs";
export default {
  name: "ProductEdit",
  props: ["value", "curNode", "curRow"],
  data() {
    return {
      productInfo: {
        relatedId: ""
      },
      providerList: [],
      productTypeList: [],
      billFrequencyList: [],
      fileList: [],
      serviceInfo: {
        list: [],
        curService: {},
        checkId: "",
        checkLabel: "",
        orginal: [],
        dialogVisible: false
      },
      priceInfo: [],
      rules: {
        prodName: [
          { required: true, message: "请输入产品名称", trigger: "blur" },
          { min: 1, max: 20, message: "长度在 1 到 20 个字符", trigger: "blur" }
        ]
      },
      keyWord: ""
    };
  },
  mounted() {
    this.curRow = this.curRow || {};
    this.initProductType();
    this.initBillFrequency();
    this.initProvider();
    this.initService();
    this.productInfo.prodType = this.curNode.id;
    if (this.curRow.productId) {
      this.initData();
    }
  },
  methods: {
    initData() {
      this.axios({
        method: "post",
        url: "/a/operate/products/getProductInfos",
        params: {
          sign: 1,
          productId: this.curRow.productId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.productInfo = res.data.data.productInfo;
            this.priceInfo = res.data.data.levePrice;
            this.fileList[0] = res.data.data.productInfo.secIcon;
            this.fileList[1] = res.data.data.productInfo.productDescs;
            this.fileList[2] = res.data.data.productInfo.useGuide;
            this.fileList[3] = res.data.data.productInfo.example1;
            this.fileList[4] = res.data.data.productInfo.example2;
            this.fileList[5] = res.data.data.productInfo.example3;

            this.serviceInfo.checkId = this.serviceInfo.orginal = this.productInfo.relatedId;
            this.serviceInfo.checkLabel = this.productInfo.relatedLabel;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initProductType() {
      this.axios({
        url: "/a/operate/products/selectProductType",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            this.productTypeList = res.data.data;
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initBillFrequency() {
      this.axios({
        url: "/a/operate/products/selectBillFrequency",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            this.billFrequencyList = res.data.data;
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initProvider() {
      this.axios({
        url: "/a/operate/products/getProvider",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            this.providerList = res.data.data;
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initService() {
      this.axios({
        url: "/a/operate/products/selectService",
        method: "post",
        params: {
          sign: 1,
          key: this.keyWord
        }
      })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            this.serviceInfo.list = res.data.data;
            if (this.serviceInfo.list.length > 0) {
              this.serviceInfo.curService = this.serviceInfo.list[0];
            } else {
              this.serviceInfo.curService = {};
            }
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    saveProduct() {
      if (!this.checkValid()) {
        return;
      }
      let _this = this;
      let data = Object.assign(_this.productInfo, {
        sign: 1,
        fatherId: _this.productInfo.serviceType || _this.curNode.id
      });

      let tempPrice = this.priceInfo;
      if (tempPrice.length === 1) {
        tempPrice.push({});
      } else if (tempPrice.length === 0) {
        tempPrice = [{}, {}];
      }
      tempPrice.map((val, idx) => {
        tempPrice[idx] = JSON.stringify(tempPrice[idx]);
      });

      delete data.secIcon;
      delete data.productDescs;
      delete data.useGuide;
      delete data.example1;
      delete data.example2;
      delete data.example3;
      let formData = new FormData();
      for (let key in data) {
        formData.append(key, data[key]);
      }
      this.fileList.map((fileList, fileIdx) => {
        let fileListKey = "";
        let urlKey = "";
        switch (fileIdx) {
          case 0:
            fileListKey = "secIcon";
            urlKey = "files";
            break;
          case 1:
            fileListKey = "productDescs";
            urlKey = "productDescsPath";
            break;
          case 2:
            fileListKey = "useGuide";
            urlKey = "useGuidePath";
            break;
          case 3:
            fileListKey = "example1";
            break;
          case 4:
            fileListKey = "example2";
            break;
          case 5:
            fileListKey = "example3";
            break;
        }
        fileList.map((val, idx) => {
          if (val.raw) {
            formData.append(fileListKey, val.raw);
          } else if (fileIdx === 0 || fileIdx === 1 || fileIdx === 2) {
            formData.append(urlKey, val.url);
          } else if (fileIdx === 3 || fileIdx === 4 || fileIdx === 5) {
            formData.append(fileListKey, val.url);
          }
        });
        if (fileList.length <= 0) {
          formData.append(fileListKey, "");
          if (fileIdx === 0 || fileIdx === 1 || fileIdx === 2) {
            formData.append(urlKey, "");
          }
        }
      });

      tempPrice.map((val, idx) => {
        formData.append("price", val);
      });

      this.axios
        .post("/a/operate/products/save", formData, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            this.$emit("input", false);
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    checkValid() {
      let flag = false;
      this.$refs.from.validate(valid => {
        if (valid) {
          flag = true;
        }
      });
      return flag;
    },
    changeServiceStatus() {
      this.serviceInfo.list.map((val, idx) => {
        val.services.map((sVal, sIdx) => {
          if (
            sVal.serviceId === this.serviceInfo.checkId &&
            this.productInfo.relatedId !== sVal.serviceId
          ) {
            this.initPriceList();
            this.serviceInfo.checkLabel = sVal.servName;
            this.productInfo.relatedId = sVal.serviceId;
          }
        });
      });
      this.keyWord = "";
    },
    initPriceList() {
      this.axios({
        url: "/a/operate/products/getServiceLevel",
        method: "post",
        params: {
          sign: 1,
          serviceId: this.serviceInfo.checkId
        }
      })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            this.priceInfo = res.data.data;
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    changeFile(file, fileList, idx) {
      this.fileList[idx] = fileList;
      console.log(this.fileList, fileList);
    },
    removeFile(file, fileList, idx) {
      this.fileList[idx] = [];
      console.log(this.fileList);
    },
    handleExceed(file, fileList, idx) {
      this.$alert("此处只允许上传一张图片", "提示", {
        confirmButtonText: "确定"
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
h3 {
  margin-bottom: 20px;
}

.el-checkbox {
  margin-bottom: 10px;
}
/deep/.el-dialog__body {
  padding-top: 10px;
}
.flex {
  position: initial !important;
  .left-wrapper {
    min-width: 200px !important;
    max-width: 200px;
    padding: 0 !important;
    div {
      height: 30px;
      line-height: 30px;
      cursor: pointer;
      padding-left: 20px;
      &.active {
        background-color: #e1e1e1;
      }
    }
  }
  .right-wrapper {
    padding: 0 !important;
    .el-radio {
      height: 30px;
      line-height: 30px;
    }
  }
}
* #app .search-box {
  width: auto;
}
</style>



