<template>
  <div class="hello">
    <h3> 服务创建</h3>
    <el-form
      ref="from"
      :model="serviceInfo"
      label-width="100px"
      :rules="rules"
    >
      <el-form-item
        label="服务名称"
        prop="servName"
      >
        <el-input
          v-model="serviceInfo.servName"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
      <el-form-item label="服务类型">
        <el-input
          placeholder="请输入内容"
          :disabled="true"
          v-model="serviceInfo.serviceTypeLabel"
        ></el-input>
      </el-form-item>
      <el-form-item label="是否主服务">
        <el-select
          v-model="serviceInfo.isMain"
          placeholder="请选择"
        >
          <el-option
            value="1"
            label="是"
          ></el-option>
          <el-option
            value="0"
            label="否"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="依赖服务">
        <el-col
          :span="6"
          class="mr20"
        >
          <el-input
            v-model="dependService.checkListLabel"
            placeholder="请输入内容"
            :disabled="true"
          ></el-input>
        </el-col>
        <el-button
          type="primary"
          @click="dependService.dialogVisible = true"
        >选 择</el-button>
      </el-form-item>
      <el-form-item label="互斥服务">
        <el-col
          :span="6"
          class="mr20"
        >
          <el-input
            v-model="excludeService.checkListLabel"
            placeholder="请输入内容"
            :disabled="true"
          ></el-input>
        </el-col>
        <el-button
          type="primary"
          @click="excludeService.dialogVisible = true"
        >选 择</el-button>
      </el-form-item>
      <el-form-item label="是否启用">
        <el-select
          v-model="serviceInfo.status"
          placeholder="请选择"
        >
          <el-option
            value="1"
            label="启用"
          ></el-option>
          <el-option
            value="0"
            label="不启用"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="服务提供方">
        <el-input
          v-model="serviceInfo.servProvName"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
      <el-form-item label="服务描述">
        <el-input
          v-model="serviceInfo.servDesc"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
      <h3> 服务等级与服务指标</h3>
      <ServiceLevelSetting
        :levelInfoLevel.sync="levelInfoLevel"
        :levelInfoIndex.sync="levelInfoIndex"
      ></ServiceLevelSetting>
    </el-form>
    <div
      slot="footer"
      class="dialog-footer"
    >
      <el-button
        type="primary"
        plain
        @click="$emit('input',false)"
      >取 消</el-button>
      <el-button
        type="primary"
        @click="saveService()"
      >确 定</el-button>
    </div>
    <el-dialog
      title="依赖服务列表"
      :visible.sync="dependService.dialogVisible"
      width="50%"
      @closed="changeDependDisabledStatus()"
    >
      <el-checkbox-group v-model="dependService.checkList">
        <el-checkbox
          :label="v.serviceId"
          v-for="(v,i) in dependService.list"
          :key="i"
          :disabled="v.disabled"
        >{{v.servName}}</el-checkbox>
      </el-checkbox-group>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          plain
          @click="dependService.dialogVisible = false; dependService.checkList = JSON.parse(JSON.stringify(dependService.orginalList));"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="dependService.dialogVisible = false; dependService.orginalList = JSON.parse(JSON.stringify(dependService.checkList));"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="互斥服务列表"
      :visible.sync="excludeService.dialogVisible"
      @closed="changeExcludeDisabledStatus()"
      width="50%"
    >
      <el-checkbox-group v-model="excludeService.checkList">
        <el-checkbox
          :label="v.serviceId"
          v-for="(v,i) in excludeService.list"
          :key="i"
          :disabled="v.disabled"
        >{{v.servName}}</el-checkbox>
      </el-checkbox-group>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          plain
          @click="excludeService.dialogVisible = false; excludeService.checkList = JSON.parse(JSON.stringify(excludeService.orginalList));"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="excludeService.dialogVisible = false; excludeService.orginalList = JSON.parse(JSON.stringify(excludeService.checkList));"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import qs from "qs";
import ServiceLevelSetting from "./ServiceLevelSetting.vue";
export default {
  name: "ServiceEdit",
  components: { ServiceLevelSetting },
  props: ["value", "curNode", "curRow"],
  data() {
    return {
      serviceInfo: {},
      dependService: {
        list: [],
        checkListLabel: "",
        checkList: [],
        orginalList: [],
        dialogVisible: false
      },
      excludeService: {
        list: [],
        checkListLabel: "",
        checkList: [],
        orginalList: [],
        dialogVisible: false
      },
      levelInfoIndex: [],
      levelInfoLevel: [],
      rules: {
        servName: [
          { required: true, message: "请输入服务名称", trigger: "blur" },
          { min: 1, max: 20, message: "长度在 1 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.curRow = this.curRow || {};
    this.initServiceList(1);
    this.initServiceList(2);
    if (this.curRow.serviceId) {
      this.initData();
    } else {
      this.levelInfoLevel = [];
      this.levelInfoIndex = [];
      this.serviceInfo.serviceTypeLabel = this.curNode.label;
    }
  },
  methods: {
    initData() {
      this.axios({
        method: "post",
        url: " /a/operate/serServices/getServiceObject",
        params: {
          sign: 1,
          serviceId: this.curRow.serviceId
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.serviceInfo = res.data.data.serviceInfo;
            this.levelInfoLevel = res.data.data.serviceLevelInfo;
            this.levelInfoIndex = res.data.data.quotaInfo;
            this.dependService.checkList = JSON.parse(res.data.data.serviceInfo.dependsService);
            this.dependService.orginalList = JSON.parse(res.data.data.serviceInfo.dependsService);
            this.excludeService.checkList = JSON.parse(res.data.data.serviceInfo.mutexService);
            this.excludeService.orginalList = JSON.parse(res.data.data.serviceInfo.mutexService);
            console.log(this.dependService.checkList,this.excludeService.checkList);
            this.changeExcludeDisabledStatus();
            this.changeDependDisabledStatus();
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert(err, "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initServiceList(type) {
      this.axios({
        url: "/a/operate/serServices/getAllServices",
        method: "post",
        params: {
          sign: type,
          serviceId: this.curRow.serviceId
        }
      })
        .then(res => {
          if ((res.data.tranCode = "XX00")) {
            if (type === 1) {
              this.dependService.list = JSON.parse(
                JSON.stringify(res.data.data)
              );
            } else {
              this.excludeService.list = JSON.parse(
                JSON.stringify(res.data.data)
              );
            }
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    saveService() {
      if (!this.checkValid()) {
        return;
      }
      let _this = this;
      const data = Object.assign(
        _this.serviceInfo,
        {
          sign: 1,
          fatherId: _this.serviceInfo.serviceType || _this.curNode.id
        },
        {
          levels: JSON.parse(JSON.stringify(this.levelInfoLevel || [])),
          quatos: JSON.parse(JSON.stringify(this.levelInfoIndex || []))
        }
      );
      if (data.levels.length === 1) {
        data.levels.push({});
      } else if (data.levels.length === 0) {
        data.levels = [{}, {}];
      }
      if (data.quatos.length === 1) {
        data.quatos.push({});
      } else if (data.quatos.length === 0) {
        data.quatos = [{}, {}];
      }

      data.levels.map((val, idx) => {
        data.levels[idx] = JSON.stringify(data.levels[idx]);
      });
      data.quatos.map((val, idx) => {
        data.quatos[idx] = JSON.stringify(data.quatos[idx]);
      });

      data.dependsService = this.dependService.checkList;
      data.mutexService = this.excludeService.checkList;

      this.axios
        .post(
          "/a/operate/serServices/saveService",
          qs.stringify(data, { indices: false })
        )
        .then(res => {
          this.loading = false;
          if ((res.data.tranCode = "XX00")) {
            this.$emit("input", false);
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    changeDependDisabledStatus() {
      let checkListLabel = [];
      this.excludeService.list.map((val, idx) => {
        if (this.dependService.checkList.indexOf(val.serviceId) >= 0) {
          this.excludeService.list[idx].disabled = true;
        } else {
          this.excludeService.list[idx].disabled = false;
        }
        if (this.dependService.checkList.indexOf(val.serviceId) >= 0) {
          checkListLabel.push(val.servName);
        }
      });
      this.dependService.checkListLabel = checkListLabel.toString();
    },
    changeExcludeDisabledStatus() {
      let checkListLabel = [];
      this.dependService.list.map((val, idx) => {
        if (this.excludeService.checkList.indexOf(val.serviceId) >= 0) {
          this.dependService.list[idx].disabled = true;
        } else {
          this.dependService.list[idx].disabled = false;
        }
        if (this.excludeService.checkList.indexOf(val.serviceId) >= 0) {
          checkListLabel.push(val.servName);
        }
      });
      this.excludeService.checkListLabel = checkListLabel.toString();
    },
    checkValid() {
      let flag = false;
      this.$refs.from.validate(valid => {
        if (valid) {
          flag = true;
        }
      });
      return flag;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin-bottom: 20px;
}

.el-checkbox {
  margin-bottom: 10px;
}
</style>



