<template>
  <div class="full-wrapper">
    <div class="title">
      <h3>发票管理</h3>
      <el-form
        class="demo-form-inline mb20"
        label-width="80px"
      >
        <el-row>
          <el-col
            :lg="5"
            :sm="10"
            class="search-col pull-right"
          >
            <el-form-item label-width="0px">
              <div class="search-box">
                <el-input
                  placeholder="请输入关键字"
                  v-model="keyWord"
                  @keyup.enter="initData()"
                ></el-input>
                <el-button
                  type="text"
                  class="el-icon-search"
                  @click="initData()"
                >
                </el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="main-content">

      <el-badge
        :value="undealTotal"
        class="item"
      ></el-badge>
      <el-tabs
        v-model="activeIndex"
        @tab-click="()=>{initData()}"
      >

        <el-tab-pane
          label="全部"
          name="0"
        >
        </el-tab-pane>
        <el-tab-pane
          label="未处理"
          name="1"
        ></el-tab-pane>
        <el-tab-pane
          label="已解决"
          name="2"
        ></el-tab-pane>
        <el-tab-pane
          label="已撤回"
          name="4"
        ></el-tab-pane>
      </el-tabs>
      <div>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
        border
        stripe
        style="width: 100%"
        class="mb20"
      >
        <el-table-column
          prop="custName"
          label="客户名称"
        >
        </el-table-column>
        <el-table-column
          prop="prodName"
          label="账期/产品"
        >
        </el-table-column>
        <el-table-column
          prop="remark1"
          label="可开票金额"
        >
        </el-table-column>
        <el-table-column
          prop="money"
          label="实付金额"
        >
        </el-table-column>
        <el-table-column
          prop="nameInvoice"
          label="联系人"
        >
        </el-table-column>
        <el-table-column
          prop="phone"
          label="联系电话"
        >
        </el-table-column>
        <el-table-column
          prop="staticIdLabel"
          label="状态"
        >
        <template slot-scope="scope">
            <span :class="{'warning':scope.row.staticId=='1','success':scope.row.staticId=='2','danger':scope.row.staticId=='3','info':scope.row.staticId=='4'}">{{scope.row.staticIdLabel}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作"><template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              v-if="scope.row.staticId==='1'"
              @click="()=>{dialogVisibleDetail=true; isEdit=true; curInvoiceInfo=scope.row;}"
            >查看申请</el-button>
            <el-button
              type="text"
              size="small"
              v-if="scope.row.staticId!=='1'"
              @click="()=>{dialogVisibleDetail=true; isEdit=false; curInvoiceInfo=scope.row;}"
            >详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      v-if="tableData.length>0"
      layout="prev, pager, next"
      :current-page="pageNo"
      :page-size="pageSize"
      :total="total"
      @current-change="initData"
    >
    </el-pagination>
    <el-dialog
      title="发票申请处理"
      :visible.sync="dialogVisibleDetail"
      width="50%"
    >
      <p>
        当前状态：{{curInvoiceInfo.staticIdLabel}}
      </p>
      <el-form
        ref="form"
        label-width="100px"
      >
        <el-col :span="24">
          <el-form-item label="产品/账期:">
            {{curInvoiceInfo.prodName}}
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="可开票金额:">
            {{curInvoiceInfo.remark1}}
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="实付金额:">
            {{curInvoiceInfo.money}}
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="退订处理:">
            <el-radio-group
              :disabled="!isEdit"
              v-model="curInvoiceInfo.staticId"
            >
              <el-radio label="2">审核通过，已开具发票</el-radio>
              <el-radio label="3">审核不通过，驳回申请</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col
          :span="24"
          class="mt20"
        >
          <el-form-item label="备注:">
            <span v-if="!isEdit"> {{curInvoiceInfo.remark4}}</span>
            <el-input
              v-if="isEdit"
              type="textarea"
              v-model="curInvoiceInfo.remark4"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="default"
          v-if="isEdit"
          @click="dialogVisibleDetail = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="saveInvoice()"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "InvoiceManagement",
  data() {
    return {
      activeIndex: "0",
      isEdit: false,
      undealTotal: 0,
      keyWord: "",
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
      curInvoiceInfo: {},
      dialogVisibleDetail: false,
      dealStatus: false
    };
  },
  mounted() {
    this.initData();
    this.initDataUndeal();
  },
  methods: {
    initData(pageNo) {
      this.loading = true;
      this.pageNo = pageNo || 1;
      this.axios({
        url: "/a/operate/invoiceReq/invoiceList",
        method: "post",
        params: {
          sign: this.activeIndex,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWord
        }
      })
        .then(res => {
          this.loading = false;
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data;
            this.total = res.data.total;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.loading = false;
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    initDataUndeal() {
      this.axios({
        url: "/a/operate/invoiceReq/getUndealCount",
        method: "post",
        params: {
          sign: this.activeIndex,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          key: this.keyWord
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.undealTotal = res.data.data;
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    },
    saveInvoice() {
      if (!this.isEdit) {
        this.dialogVisibleDetail = false;
        return;
      }
      this.axios({
        url: "/a/operate/invoiceReq/save",
        method: "post",
        params: {
          sign: 3,
          remark4: this.curInvoiceInfo.remark4,
          staticId:this.curInvoiceInfo.staticId,
          id: this.curInvoiceInfo.id
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.dialogVisibleDetail = false;
            this.initData(this.pageNo);
            this.initDataUndeal();
          } else {
            this.$alert(res.data.msg, "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$alert("服务器报错", "提示", {
            confirmButtonText: "确定"
          });
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.search-box {
  float: inherit !important;
  width: auto !important;
  margin: 0 20px;
}

.el-button a {
  text-decoration: none;
  color: #ffffff;
}
.el-form-item {
  margin-bottom: 0px;
}
body #app .title {
  border-bottom: none;
}
.main-content {
  position: relative;
  .el-badge {
    position: absolute;
    &:nth-child(1) {
      left: 110px;
      top: 0px;
    }
  }
}
form {
  overflow: hidden;
}
.el-dialog__body {
  padding-top: 0;
}
</style>



