<template>
  <div class="panel">
    <el-steps
      :active="curStep"
      finish-status="success"
    >
      <el-step title="设置服务等级"></el-step>
      <el-step title="设置等级指标"></el-step>
      <el-step title="完成"></el-step>
    </el-steps>
    <!-- 设置服务等级 -->
    <section v-show="curStep === 0">
      <el-table
        class="mt20"
        v-loading="loading"
        :data="tableData"
        border
        stripe
        style="width: 100%"
      >
        <el-table-column
          prop="name"
          label="等级名称"
        >
        </el-table-column>
        <el-table-column
          prop="description"
          label="等级描述"
        >
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注"
        >
        </el-table-column>
        <el-table-column label="操作"><template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="editLevel(scope.row,scope.$index)"
            >编辑</el-button>
            <el-button
              type="text"
              size="small"
              @click="removeLevel(scope.$index)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <span
        class="el-icon-circle-plus-outline"
        @click="dialogVisibleLevel=true; curLevelInfo={};"
      ></span>
      <!-- <el-pagination
        background
        v-if="tableData.length>0"
        layout="prev, pager, next"
        :current-page="pageNo"
        :page-size="pageSize"
        :total="total"
        @current-change="changePage"
      >
      </el-pagination> -->
      <div class="ibox-footer text-right mt20">
        <el-button
          type="primary"
          plain
          class="btn btn-primary"
          @click="clickNextStep()"
        >下一步</el-button>
      </div>
    </section>
    <!-- 设置等级指标 -->
    <section v-show="curStep===1">

      <el-table
        class="mt20"
        v-loading="loading"
        :data="tableDataIndex"
        border
        stripe
        style="width: 100%"
      >

        <el-table-column
          prop="name"
          label="指标名称"
        >
        </el-table-column>
        <el-table-column
          :prop="''+i"
          :label="v.name"
          v-for="(v,i) in tableData"
          :key="i"
        >
        </el-table-column>
        <el-table-column
          prop="orderCycle"
          label="操作"
        ><template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="editIndex(scope.row,scope.$index)"
            >编辑</el-button>
            <el-button
              type="text"
              size="small"
              @click="removeIndex(scope.$index)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <span
        class="el-icon-circle-plus-outline"
        @click="dialogVisibleIndex=true; curIndexInfo={};"
      ></span>
      <!-- <el-pagination
          background
          v-if="tableData.length>0"
          layout="prev, pager, next"
          :current-page="pageNo"
          :page-size="pageSize"
          :total="total"
          @current-change="changePage"
        >
        </el-pagination> -->
      <div class="ibox-footer text-right">
        <el-button
          type="primary"
          plain
          class="btn btn-primary btn-outline mt20"
          @click="clickPreStep()"
        >上一步</el-button>
        <el-button
          type="primary"
          plain
          class="btn btn-primary mt20"
          @click="clickNextStep()"
        >下一步</el-button>
      </div>
    </section>
    <!-- 完成 -->
    <section v-show="curStep===2">
      <el-button
        type="primary"
        plain
        class="mt20"
        @click="clickPreStep()"
      >返回修改</el-button>
      <el-tabs
        type="border-card"
        class="mt20"
      >
        <el-tab-pane>
          <span slot="label"> 服务等级</span>
          <el-table
            class="mt20"
            v-loading="loading"
            :data="tableData"
            border
            stripe
            style="width: 100%"
          >
            <el-table-column
              prop="name"
              label="等级名称"
            >
            </el-table-column>
            <el-table-column
              prop="description"
              label="等级描述"
            >
            </el-table-column>
            <el-table-column
              prop="remark"
              label="备注"
            >
            </el-table-column>
          </el-table>
          <!-- <el-pagination
            background
            v-if="tableData.length>0"
            layout="prev, pager, next"
            :current-page="pageNo"
            :page-size="pageSize"
            :total="total"
            @current-change="changePage"
          >
          </el-pagination> -->
        </el-tab-pane>
        <el-tab-pane label="等级指标">
          <el-table
            class="mt20"
            v-loading="loading"
            :data="tableDataIndex"
            border
            stripe
            style="width: 100%"
          >

            <el-table-column
              prop="name"
              label="指标名称"
            >
            </el-table-column>
            <el-table-column
              v-for="(v,i) in tableData"
              :prop="''+i"
              :label="v.name"
              :key="i"
            >
            </el-table-column>
          </el-table>
          <!-- <el-pagination
            background
            v-if="tableData.length>0"
            layout="prev, pager, next"
            :current-page="pageNo"
            :page-size="pageSize"
            :total="total"
            @current-change="changePage"
          >
          </el-pagination> -->

        </el-tab-pane>
      </el-tabs>
    </section>
    <el-dialog
      title="编辑服务等级"
      :visible.sync="dialogVisibleLevel"
      width="50%"
    >
      <el-form
        ref="form"
        label-width="100px"
      >
        <el-col :span="24">
          <el-form-item label="等级名称:">
            <el-input v-model="curLevelInfo.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="等级描述:">
            <el-input
              type="textarea"
              v-model="curLevelInfo.description"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="备注:">
            <el-input v-model="curLevelInfo.remark"></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleLevel = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="saveLevel()"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="编辑等级指标"
      :visible.sync="dialogVisibleIndex"
      width="50%"
    >
      <el-form
        ref="form"
        label-width="100px"
      >
        <el-col :span="24">
          <el-form-item label="指标名称:">
            <el-input v-model="curIndexInfo.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col
          :span="24"
          v-for="(v,i) in tableData"
          :key="i"
        >
          <el-form-item :label="v.name+':'">
            <el-input
              type="textarea"
              v-model="curIndexInfo[i]"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleIndex = false"
        >取 消</el-button>
        <el-button
          type="primary"
          @click="saveIndex()"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "ServiceLevelSetting",
  props: ["levelInfoLevel", "levelInfoIndex"],
  data() {
    return {
      curStep: 0,
      dialogVisibleLevel: false,
      dialogVisibleIndex: false,
      loading: false,
      tableData: [],
      tableDataIndex: [],
      curLevelInfo: {},
      curIndexInfo: []
    };
  },
  methods: {
    clickPreStep() {
      this.curStep--;
    },
    clickNextStep() {
      this.curStep++;
    },
    editLevel(row, index) {
      this.dialogVisibleLevel = true;
      this.curLevelInfo = JSON.parse(JSON.stringify(row));
      this.curLevelInfo.idx = index;
    },
    saveLevel() {
      if (this.curLevelInfo.idx !== undefined) {
        const index = this.curLevelInfo.idx;
        delete this.curLevelInfo.idx;
        for (let key in this.curLevelInfo) {
          this.tableData[index][key] = this.curLevelInfo[key];
        }
      } else {
        this.tableData.push(this.curLevelInfo);
      }
      this.dialogVisibleLevel = false;
      this.$emit("levelInfoLevel", this.tableData);
    },
    removeLevel(index) {
      this.$confirm("确认删除该服务等级？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.tableData.splice(index, 1);
        if (this.tableData.length <= 0) {
          this.tableDataIndex = [];
        } else {
          this.tableDataIndex.map((val, idx) => {
            for (let key in val) {
              if (index == key) {
                let i = index;
                while (i <= this.tableData.length) {
                  if (i === this.tableData.length) {
                    delete this.tableDataIndex[idx][i];
                  } else {
                    this.tableDataIndex[idx][i] = this.tableDataIndex[idx][
                      i + 1
                    ];
                  }
                  i++;
                }
              }
            }
          });
        }
        console.log(this.tableDataIndex);
      }).catch(()=>{});
    },
    editIndex(row, index) {
      this.dialogVisibleIndex = true;
      this.curIndexInfo = JSON.parse(JSON.stringify(row));
      this.curIndexInfo.idx = index;
    },
    saveIndex() {
      if (this.curIndexInfo.idx !== undefined) {
        const index = this.curIndexInfo.idx;
        delete this.curIndexInfo.idx;
        for (let key in this.curIndexInfo) {
          this.tableDataIndex[index][key] = this.curIndexInfo[key];
        }
      } else {
        this.tableDataIndex.push(this.curIndexInfo);
      }
      this.dialogVisibleIndex = false;
      this.$emit("levelInfoIndex", this.tableDataIndex);
    },
    removeIndex(index) {
      this.$confirm("确认删除该等级指标？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.tableDataIndex.splice(index, 1);
      });
    }
  },
  watch: {
    levelInfoLevel(val, oldVal) {
      this.tableData = val;
    },
    levelInfoIndex(val, oldVal) {
      this.tableDataIndex = val;
    }
  }
};
</script>

<style lang="less" scoped>
.panel {
  border: 1px solid #e1e1e1;
  border-radius: 4px;
  padding: 40px 60px;
  margin-bottom: 40px;
  section:nth-child(2),
  section:nth-child(3) {
    position: relative;
    span.el-icon-circle-plus-outline {
      position: absolute;
      right: -40px;
      bottom: 76px;
      font-size: 30px;
      color: #999999;
      cursor: pointer;
    }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
}
</style>



